
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Card from 'vux/src/components/card/index.vue'


export default {
  components: {
    Group,
    Cell,
    Grid,
    GridItem,
    Divider,
    Panel,
    Card
  },
  data () {
    return {
      experience: [
        '2014年加入去哪儿网，开始了没有白天黑夜的客服生活',
        '2015年转为培训老师，带出了一批又一批的学生们',
        '2016年，开始了出差培训的生活，全国各地跑',
        '2016年11月，遇上了他，我的男朋友，从此开始了相亲相爱的生活'
      ],
      msg: 'your love',
      header: '我的资料',
      list: [{
        title: '自我评价',
        desc: `本人对待工作认真负责，注重团队协作，能够活跃团队气氛，适应能力强。1年内，从客服成长为多技能培训讲师，目前是酒店项目负责人。
        多次出差，参与各个外包公司的新人招聘，宣讲，培训等环节。
        具有优秀的学习能力，管理能力，创新能力，沟通能力。喜欢做有挑战性的工作，能承受工作压力。`
      }],
      type: '2',
      footer: {
        url: 'http://www.baidu.com',
        title: 'baidu'
      }
    }
  }
}



